.header {
  background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56));
  position: relative;

  @include mobile {
    background-image: url(../images/bg/header_main_320px.webp);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 230px;
    padding-top: 40px;
    // padding-bottom: 90px;
    padding-bottom: 10px;

    @include retinaBgImg {
      background-image: url(../images/bg/header_main_320px@2x.webp);
    }
  }

  @include tablet {
    background-image: url(../images/bg/header_main_768px.webp);
    height: 216px;

    // padding-bottom: 80px;

    @include retinaBgImg {
      background-image: url(../images/bg/header_main_768px@2x.webp);
    }
  }

  @include desktop {
    background-image: url(../images/bg/header_main_1280px.webp);

    @include retinaBgImg {
      background-image: url(../images/bg/header_main_1280px@2x.webp);
    }
  }
}

.header--lib {
  @include mobile {
    padding-bottom: 15px;
    background-image: url(../images/bg/header_lib_320px.webp);

    @include retinaBgImg {
      background-image: url(../images/bg/header_lib_320px@2x.webp);
    }
  }

  @include tablet {
    background-image: url(../images/bg/header_lib_768px.webp);

    @include retinaBgImg {
      background-image: url(../images/bg/header_lib_768px@2x.webp);
    }
  }

  @include desktop {
    background-image: url(../images/bg/header_lib_1280px.webp);

    @include retinaBgImg {
      background-image: url(../images/bg/header_lib_1280px@2x.webp);
    }
  }
}

.header__conrainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

