//* Анімація логотипу

.header__logo {
  display: flex;
  justify-content: start;
  align-items: center;
}

.header__logo:hover .animation-wrapper,
.header__logo:focus .animation-wrapper {
  animation: running-tape 2s linear infinite;
}

.animation-running-tape {
  height: 22px;
  width: 22px;
  border-radius: 4px;
  overflow: hidden;
}

.animation-running-tape img {
  display: block;
  width: 22px;
}

@keyframes running-tape {
  0% {
    transform: translateY(0%);
  }

  100% {
    transform: translateY(-65%);
  }
}

.header__logo-name {
  @include only-mobile {
    font-size: 0;
  }

  font-size: 30px;
  color: var(--bg-main-color);
  margin-left: 10px;
}

.header__nav {
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__nav-item+.header__nav-item {
  margin-left: 40px;
}

.header__nav-link {
  font-size: 12px;
  color: var(--bg-main-color);
}

.header__nav-link--current {
  position: relative;

  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -3px;
    display: block;
    width: 100%;
    height: 3px;
    background-color: #ff001b;
  }
}

.header__form {
  width: 100%;
  display: flex;
  // margin-top: auto;
  margin-left: auto;
  margin-right: auto;
  border-bottom: 0.5px solid var(--bg-main-color);

  @include only_mobile {
    width: 280px;
  }

  @include tablet {
    width: 336px;
  }

  @include desktop {
    width: 394px;
  }
}

.header__form input {
  width: inherit;
  background-color: transparent;
  border: none;
  outline: none;

  font-size: 14px;
  color: var(--bg-main-color);
}

.search-svg {
  fill: var(--bg-main-color);
}

.filter__form {
  display: flex;
  // flex-wrap: wrap;
  justify-content: center;
  // margin-top: 30px;
  padding: 0 5px;
}

.filter__form-select {
  @include only-mobile {
    margin-right: 3px;
  }

  display: flex;
  justify-content: center;
  padding: 5px;
  margin-right: 15px;
  background-color: transparent;
  border: 1px solid var(--bg-main-color);
  border-radius: 15px;
  color: var(--hover-color);
  font-size: 14px;
  text-align: center;
  //
}

#yearForm {
  width: 65px;
}

.filter__form-select>option {
  align-items: center;
  background-color: var(--btn-text-color);
  padding: 5px;
}

.filter__form.is-hidden {
  visibility: hidden;
}

.button__reset {
  width: 70px;
  background-color: transparent;
  border: 1px solid var(--bg-main-color);
  border-radius: 15px;

  font-size: 14px;
  color: var(--hover-color);

  &:hover,
  &:focus {
    background-color: var(--hover-color);
    color: var(--btn-text-color);
  }
}

.filter__form-select--genres,
.filter__form-select--year {
  @include only-mobile {
    display: none;
  }
}